@import "https://fonts.googleapis.com/css2?family=Outfit:wght@400;700&display=swap";
.attribution {
  text-align: center;
  font-size: 11px;
}

.attribution a {
  color: #3e52a3;
}

:root {
  --bg-color-app: #d5e1ef;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Outfit, "sans-serif";
}

body {
  width: 100%;
  min-height: 100vh;
  background-color: var(--bg-color-app);
  flex-direction: column;
  justify-content: space-between;
  padding: .5rem;
  font-size: 15px;
  display: flex;
}

main {
  background-color: #fff;
  border-radius: 16px;
  align-self: center;
  margin: auto 2em;
  padding: 1em;
}

.qr-img {
  width: 100%;
}

.section-item {
  text-align: center;
  margin: 1em 0;
}

.section-item.title {
  font-size: 1.2em;
}

.section-item.desc {
  color: #a3a9b1e0;
}

@media (min-width: 769px) {
  body {
    font-size: 18px;
  }
}

@media (min-width: 1025px) {
  main {
    width: 28%;
  }
}

